
import { camera, trash, close } from 'ionicons/icons';
import { actionSheetController, IonPage, IonHeader, IonFab, IonFabButton, 
         IonIcon, IonToolbar, IonTitle, IonContent, IonImg, IonGrid, 
         IonRow, IonCol } from '@ionic/vue';
// other imports
import { usePhotoGallery, Photo } from '@/composables/usePhotoGallery'

export default  {
  name: 'Tab2',
  components: { IonPage, IonHeader, IonFab, IonFabButton, IonIcon, 
         IonToolbar, IonTitle, IonContent, IonGrid, IonCol, IonRow },
  setup() {
    const { photos, takePhoto, deletePhoto } = usePhotoGallery()
    
    const showActionSheet = async (photo: Photo) => {
      const actionSheet = await actionSheetController.create({
        header: 'Photos',
        buttons: [{
          text: 'Delete',
          role: 'destructive',
          icon: trash,
          handler: () => {
            deletePhoto(photo);
        }}, {
          text: 'Cancel',
          icon: close,
          role: 'cancel',
          handler: () => {
            // Nothing to do, action sheet is automatically closed
          }
        }]
      });
      await actionSheet.present();
    }

    return {
      photos, takePhoto, showActionSheet, camera, trash, close
    }
  }
}
